
.Playbar{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80px;
  background: #282828;
  z-index: 99;
  padding: 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  }

.left {
    width: 200px;
  }
.artist {
      font-size: 14px;
      color: '#999999';
      margin-top: 5px;
    }
  
.middle {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.fa-play,.fa-pause {
    font-size: 14px;
}
.play-pause-circle {
    width: 35px;
    height: 35px;
    border: 1px solid rgb(9, 70, 161);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.progress-container {
      width: 600px;
      height: 5px;
      position: relative;
      background-color: #488052;
      margin-top: 10px;
      display: inline-flex;
      margin: 10px 10px 0px 10px;
}
      .bar {
        height: 100%;
        background-color: rgb(167 167 167);
      }
    

  .right 
    .fa-volume-up,
    .fa-volume-off {
      font-size: 20px;
    }